import React from 'react';
import {
  ChakraProvider,
  Box,
} from '@chakra-ui/react';
import theme from './theme/theme.js'
import Header from './components/Header';
import Features from './Features';
import Gym from './Gym';
import Customer from './Customer';
import Home from './Home';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
// import { ColorModeSwitcher } from './ColorModeSwitcher';
import SrcollToTop from './components/ScrollToTop';


function App() {
  return (
    <ChakraProvider theme={theme}>
      <Router>
        <SrcollToTop />
        <Box>
          <Header />
          <Switch>
            <Route path="/crosspoint" exact component={Home} />
            <Route path="/crosspoint/features" component={Features} />
            <Route path="/crosspoint/gym" component={Gym} />
            <Route path="/crosspoint/customer" component={Customer} />
          </Switch>
        </Box>
      </Router>
    </ChakraProvider >
  );
}

// const Home = () => (
