import React from 'react'
import { NavLink as ReachLink } from 'react-router-dom'
import {
  Link,
  LinkBox,
  Text,
  Box,
  Button,
  Stack,
  Image,
  Flex,
  AspectRatio,
  chakra,
  Divider,
} from '@chakra-ui/react';

import logo from '../assets/images/logo_w.svg';
// import { ColorModeSwitcher } from '../ColorModeSwitcher';

const NavLink = chakra(Link, {
  baseStyle: {
    lineHeight: "40px",
    paddingLeft: "20px",
    _hover: {
      textDecoration: "none",
      color: "blue.100",
    },
  },
  activeStyle: {
    fontWeight: "bold",
    color: "red"
  },
})

const actvieStyle = {
  // color: "blue.500",
  color: "#22CAFF",
  _active: {
    color: "blue.500",
  },
}



const MenuIcon = () => (
  <svg
    width="24px"
    viewBox="0 0 20 20"
    xmlns="http://www.w3.org/2000/svg"
    fill="white"
  >
    <title>Menu</title>
    <path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z" />
  </svg>
);

const CloseIcon = () => (
  <svg width="24" viewBox="0 0 18 18" xmlns="http://www.w3.org/2000/svg">
    <title>Close</title>
    <path
      fill="white"
      d="M9.00023 7.58599L13.9502 2.63599L15.3642 4.04999L10.4142 8.99999L15.3642 13.95L13.9502 15.364L9.00023 10.414L4.05023 15.364L2.63623 13.95L7.58623 8.99999L2.63623 4.04999L4.05023 2.63599L9.00023 7.58599Z"
    />
  </svg>
);

const MenuToggle = ({ toggle, isOpen }) => {
  return (
    <Flex
      display={{ base: "flex", md: "none" }}
      onClick={toggle}
    >
      {isOpen ? <CloseIcon /> : <MenuIcon />}
    </Flex>
  );
};

const MenuItem = ({ children, to = "/", onClick, toggle, ...rest }) => {
  return (
    <NavLink
      as={ReachLink}
      to={to}
      activeStyle={actvieStyle}
      onClick={onClick}
      exact >
      <Text display="block" {...rest}>
        {children}
      </Text>
    </NavLink>
  );
};


const Header = () => {

  const [isOpen, setIsOpen] = React.useState(false);

  const toggle = () => setIsOpen(!isOpen);

  return (

    <Box sx={{ position: 'sticky', top: '0', }} zIndex="20" bg="gray.800" align="center" >
      <header>
        <Flex justify="center" py="12px">
          <Flex
            justify="space-between"
            // direction={{ base: "column", md: "row" }}
            w={{ base: "100%", sm: "100%", md: "80%", lg: "75%", xl: "1080px" }}
            mx="12px"
          >
            <Box w={{ base: "150px", md: "200px" }}>
              <LinkBox as={ReachLink} to="/crosspoint" exact>
                <AspectRatio maxW="200px" ratio={5 / 1}>
                  <Image objectFit="cover" src={logo} alt="crospoint" />
                </AspectRatio>
              </LinkBox>
            </Box>

            <MenuToggle toggle={toggle} isOpen={isOpen} />
            <Box display={{ base: "none", md: "block" }}>
              <Box
                display={{ base: isOpen ? "block" : "none", md: "block" }}
                flexBasis={{ base: "100%", md: "auto" }}
              >
                <Stack
                  spacing={8}
                  align="center"
                  justify={["center", "space-between", "flex-end", "flex-end"]}
                  direction={["column", "row", "row", "row"]}
                  pt={[4, 4, 0, 0]}
                >
                  <MenuItem to="/crosspoint" onClick={toggle}>Home</MenuItem>
                  <MenuItem to="/crosspoint/features" onClick={toggle}> 功能介紹 </MenuItem>
                  <MenuItem to="/crosspoint/gym" onClick={toggle}> GYM </MenuItem>
                  <MenuItem to="/crosspoint/customer" onClick={toggle}> 我們的客戶 </MenuItem>
                  <Link href="https://crosspoint-fit.web.app/" isExternal>
                    <Button
                      size="sm"
                      rounded="md"
                      // color={["primary.500", "primary.500", "white", "white"]}
                      // bg={["white", "white", "primary.500", "primary.500"]}
                      _hover={{
                        bg: ["primary.100", "primary.100", "primary.600", "primary.600"]
                      }}
                    >
                      登入
                    </Button>
                  </Link>
                </Stack>
              </Box>
            </Box>

          </Flex>
        </Flex>

        <Box
          display={{ base: isOpen ? "block" : "none", md: "none" }}
          flexBasis={{ base: "100%", md: "auto" }}
          py="32px"
        >
          <Stack
            spacing={8}
            align="center"
            justify={["center", "space-between", "flex-end", "flex-end"]}
            direction={["column", "row", "row", "row"]}
            pt={[4, 4, 0, 0]}
          >
            <MenuItem to="/crosspoint" onClick={toggle}>Home</MenuItem>
            <MenuItem to="/crosspoint/features" onClick={toggle}> 功能介紹 </MenuItem>
            <MenuItem to="/crosspoint/gym" onClick={toggle}> GYM </MenuItem>
            <MenuItem to="/crosspoint/customer" onClick={toggle}> 我們的客戶 </MenuItem>
            <Link href="https://crosspoint-fit.web.app/" isExternal onClick={toggle}>
              <Button
                size="sm"
                rounded="md"
                // color={["primary.500", "primary.500", "white", "white"]}
                // bg={["white", "white", "primary.500", "primary.500"]}
                _hover={{
                  bg: ["primary.100", "primary.100", "primary.600", "primary.600"]
                }}
              >
                登入
                    </Button>
            </Link>
          </Stack>
        </Box>
        <Divider />
      </header>
    </Box >
  )
};

export default Header;
